  min-width: 550px;
  margin: 70px auto;
}
.handle-wrapper .toolbar .handle-button {
  width: 62px;
  height: 62px;
  border-radius: 50%;
  background-color: #3AB09E;
  color: #ffffff;
  text-align: center;
  font-size: 3.2em;
  z-index: 1;
  position: fixed;
  right: 20px;
  bottom: 20px;
}
.handle-wrapper .toolbar .handle-button,
.icons {
  -webkit-transition: -webkit-all 0.5s cubic-bezier(0.87, -0.41, 0.19, 1.44);
  -webkit-transition: all 0.5s cubic-bezier(0.87, -0.41, 0.19, 1.44);
  transition: all 0.5s cubic-bezier(0.87, -0.41, 0.19, 1.44);
}
.handle-wrapper .toolbar .handle-button:after {
  content: "+";
  top: -14px;
  position: relative;
}
.handle-wrapper .toolbar .handle-button.active {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.handle-wrapper .toolbar .icons {
  width: 0%;
  overflow: hidden;
  height: 30px;
  list-style: none;
  padding: 11px 10px 11px 20px;
  background-color: #ffffff;
  -webkit-box-shadow: 1px 1px 1px 1px #DCDCDC;
